.product-additional__title {
  display: block;
  margin: 0;
  padding: 25px 0 36px;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  border-bottom: 1px solid @special-grey-2;
}

.product-additional__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.product-additional__item {
  padding: 13px 0 19px;
}

.product-additional__name {
  display: block;
  margin: 0;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
}

.product-additional__content {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  margin: 0;
  padding: 10px 0 12px;
}

.product-additional__button {
  padding: 10px 109px;
}

@media (min-width: @tablet-width) {
  .product-additional__list {
    display: flex;
    flex-direction: column;
  }
}

@media (min-width: @desktop-width) {
  .product-additional__list {
    display: flex;
    flex-direction: column;
  }

  .product-additional__item {
    width: 896px;
    display: flex;
    justify-content: space-between;
   }
  }
